import { observer } from "mobx-react";

import { useStores } from "stores/useStores";

import { Stack } from "@mui/material";

import { Product } from "components/Product/Product";
import { CatalogMenu } from "components/Catalog/CatalogMenu/CatalogMenu";
import { CatalogEditor } from "components/forms/CatalogEditor/CatalogEditor";
import { ProductCardHolder } from "components/Product/ProductCardHolder";

export const LandingPage = observer(() => {
  const { uiStore, productManagerStore, catalogStore } = useStores();

  const productCurrentCatalogIds = productManagerStore.getProductIdsForCatalog(uiStore.currentCatalogId);
  const currentCatalogName = catalogStore.findById(uiStore.currentCatalogId)?.name;

  const isCatalogSelected = Boolean(currentCatalogName);

  return <div className="landing">
    <CatalogMenu />
    {!!uiStore.catalogEdit && <CatalogEditor {...uiStore.catalogEdit} />}
    <Stack spacing={2} className="landing__item">
      {isCatalogSelected && <ProductCardHolder
        productIds={productCurrentCatalogIds}
        title={currentCatalogName ?? ""}
        type="wrap"
      />}

      {isCatalogSelected && <ProductCardHolder
        productIds={productCurrentCatalogIds}
        title={"Рекомендуем"}
        type="line"
      />}

      {/*<Product id="0" />*/}
    </Stack>
  </div>;
});
